footer {
	background-color: #dcd0cf;
	background-image: url(/dist/img/triangles2.png);
	background-repeat: no-repeat;
	background-position: right bottom;
	display: flex;
	align-content: center;
	align-items: center;
	justify-content: center;
	justify-content: space-around;
	flex-direction: column;
}

@media screen and (min-width: 768px) {
	footer {
		flex-direction: row;
		align-items: normal;
	}
}

footer .contact {
	display: flex;
	flex-direction: column-reverse;
}

@media screen and (min-width: 768px) {
	footer .contact {
		flex-direction: column;
		width: 40%;
	}
}

footer .contact .contact-head {
	display: none;
}

@media screen and (min-width: 1024px) {
	footer .contact .contact-head {
		padding-bottom: 8.5%;
		display: flex;
		flex-direction: column;
	}
}

footer .contact .contact-head h2 {
	margin-bottom: 6.8%;
	margin-top: 7.72%;
	font-size: 3rem;
}

footer .contact .contact-head span {
	font-size: 1rem;
}

footer .contact .contact-info {
	display: flex;
	flex-direction: column;
	padding-bottom: 17.529%;
}

footer .contact .contact-info .contacts {
	display: flex;
	align-items: center;
	flex-direction: row;
	padding: 0.542rem 0rem;
}

footer .contact .contact-info .contacts img {
	width: 60px;
	padding-right: 4.4%;
}

footer .contact .contact-info .contacts.address img {
	padding-bottom: 3.8rem;
}

footer .contact .contact-info .contacts.address .address-info {
	display: flex;
	flex-direction: column;
	line-height: 2.086rem;
}

footer .contact .contact-info .contacts span {
	padding-left: 0.668rem;
	font-size: 1.375rem;
}

@media screen and (min-width: 768px) {
	footer .contact .contact-info .contacts {
		width: 100%;
	}
}

@media screen and (min-width: 768px) {
	footer .contact .contact-info {
		padding-bottom: 0%;
	}
}

footer .contact .social-links {
	display: flex;
	padding: 11.47% 0rem;
}

footer .contact .social-links img {
	width: 60px;
	margin-right: 5%;
}

footer form {
	display: none;
}

@media screen and (min-width: 768px) {
	footer form {
		width: 40%;
	}
}

@media screen and (min-width: 1024px) {
	footer form {
		display: flex;
		justify-content: center;
		width: 40%;
		flex-direction: column;
	}
	footer form input {
		margin: 10px 0px;
	}
	footer form input.name {
		height: 50px;
	}
	footer form input.email {
		height: 50px;
	}
}

@media screen and (min-width: 1024px) and (min-width: 768px) {
	footer form div button {
		position: relative;
		margin: 20px 0px;
		border: none;
		height: 120px;
		font-size: 2rem;
		background-color: #193638;
		z-index: 100;
		width: 60%;
	}
}

@media screen and (min-width: 1024px) and (min-width: 768px) {
	footer form div :before {
		content: '';
		height: 100%;
		background-color: #1a3637;
		position: absolute;
		width: 70%;
		top: 0%;
		right: -31%;
		z-index: -100;
		transform: skew(-42deg, 0deg);
	}
}

@keyframes fadeIn {
	0% {
		display: none;
		opacity: 0;
	}
	100% {
		display: block;
		opacity: 1;
	}
}

@keyframes fadeOut {
	0% {
		display: block;
		opacity: 0;
	}
	100% {
		display: none;
		opacity: 1;
	}
}
